  border: 1px solid #2857bb;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.event__list-item h1 {
  margin: 0;
  font-size: 1.5rem;
  color: #2843bb
}

.event__list-item h2 {
  margin: 0;
  font-size: 1rem;
  color: #7c7c7c
}

.event__list-item p {
  margin: 0;
}